<template>
  <div class="payout-catalog">
    <div
      v-if="collapsible && payoutMethods.length > 0"
      @click="isCollapse = !isCollapse"
      class="payout-catalog__header"
    >
      <div
        class="payout-catalog__separator-icon"
        :style="{ transform: isCollapse ? 'rotate(180deg)' : 'rotate(0deg)' }"
      >
        <i class="material-icons">expand_more</i>
      </div>
      <div class="payout-catalog__separator-counter">
        <slot name="separator-label"></slot>
      </div>
      <div class="payout-catalog__separator-line">
        <hr class="w-full text-grey-02" />
      </div>
    </div>
    <transition name="slide-fade">
      <div v-show="isCollapse" class="payout-catalog__list">
        <div class="payout-methods">
          <div class="payout-methods__list">
            <div class="payout-list">
              <Tooltip
                v-for="payout in payoutMethods"
                :key="payout.id"
                :disabled="payout.clickable"
                placement="top"
                content="You can setup these Payment Methods later in settings."
              >
                <div
                  :class="[
                    'payout-list__card',
                    !payout.clickable ? 'payout-list__card--disabled' : '',
                    selectedPayoutMethodsId.includes(payout.id)
                      ? 'payout-list__card--selected'
                      : ''
                  ]"
                  @click="handleSelectPayout(payout)"
                >
                  <div
                    v-if="selectedPayoutMethodsId.includes(payout.id)"
                    class="payout-list__checklist"
                  >
                    <i class="material-icons">done</i>
                  </div>
                  <template v-if="payout.id === 21">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="6rem"
                      height="6rem"
                      viewBox="0 0 40 30"
                      fill="none"
                      class="mx-auto mt-2 mb-8"
                    >
                      <path
                        fill-rule="evenodd"
                        clip-rule="evenodd"
                        d="M4 0C1.79083 0 0 1.79083 0 4V25.9999C0 28.2092 1.79083 30.0001 4 30.0001H36C38.2092 30.0001 40 28.2092 40 26.0001V4C40 1.79083 38.2092 0 36 0H4Z"
                        :fill="
                          selectedPayoutMethodsId.includes(payout.id)
                            ? '#43a6a9'
                            : '#B0B0B0'
                        "
                      />
                      <path
                        d="M19.8633 22.1167L18.8967 21.2367C15.4633 18.1234 13.1967 16.07 13.1967 13.55C13.1967 11.4967 14.81 9.88336 16.8633 9.88336C18.0233 9.88336 19.1367 10.4234 19.8633 11.2767C20.59 10.4234 21.7033 9.88336 22.8633 9.88336C24.9167 9.88336 26.53 11.4967 26.53 13.55C26.53 16.07 24.2633 18.1234 20.83 21.2434L19.8633 22.1167Z"
                        fill="#FF69B4"
                      />
                    </svg>
                  </template>
                  <template v-else>
                    <div
                      class="payout-list__icon"
                      :style="`-webkit-mask-image: url('${payout.icon}')`"
                    ></div>
                  </template>
                  <div class="payout-list__title">
                    <slot name="title">
                      {{ payout.name }}
                    </slot>
                  </div>
                </div>
              </Tooltip>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue'
export default defineComponent({
  props: {
    payoutMethods: {
      type: Array,
      default: () => []
    },
    collapsible: {
      type: Boolean,
      default: false
    }
  },
  emits: ['change'],
  setup(props, { emit }) {
    const isCollapse = ref(true)
    const selectedPayoutMethods = ref([])
    const selectedPayoutMethodsId = computed(() =>
      selectedPayoutMethods.value.map((payout) => payout.id)
    )
    const handleSelectPayout = (selectedPayout) => {
      if (selectedPayout.clickable === false) return
      const matchedIndex = selectedPayoutMethodsId.value.findIndex(
        (payoutId) => payoutId === selectedPayout.id
      )
      const isInserted = matchedIndex > -1
      if (isInserted) {
        selectedPayoutMethods.value.splice(matchedIndex, 1)
        emit('change', selectedPayoutMethods.value)
        return
      }
      selectedPayoutMethods.value.push(selectedPayout)
      emit('change', selectedPayoutMethods.value)
    }
    return {
      isCollapse,
      selectedPayoutMethods,
      selectedPayoutMethodsId,
      handleSelectPayout
    }
  }
})
</script>
