<template>
	<div class="flex h-full">
		<div class="my-auto w-full mx-12 lg:mx-20 xl:mx-60 py-20">
			<div class="my-2">
				<h2 class="text-3xl font-semibold my-4">How will you pay for Items?</h2>
				<div class="bg-blue-lighten text-blue-04 inline-flex items-center px-4 py-2">
					<i class="material-icons mr-2">info</i>
					<p>You can automate or change the payout methods in the Payout Settings later.</p>
				</div>
			</div>
			<div class="my-8">
				<PayoutList
					:collapsible="true"
					:payout-methods="popularPayoutMethods"
					@change="selectedPayouts => handleChangeSelectedPayouts(selectedPayouts, true)">
					<template #separator-label>Most Popular</template>
				</PayoutList>
			</div>
			<div class="my-8">
				<PayoutList
					:collapsible="true"
					:payout-methods="payoutMethods"
					@change="selectedPayouts => handleChangeSelectedPayouts(selectedPayouts, false)">
					<template #separator-label>Others</template>
				</PayoutList>
			</div>
			<div class="my-2 mb-20">
				<Button
					class="mx-2"
					color="teal"
					:loading="loadingUpdatePayoutMethod"
					:disabled="isIncompleteForm"
					@click="handleUpdatePayoutMethods">
					Finish
					<i class="material-icons ml-2">east</i>
				</Button>
			</div>
		</div>
		<!-- <OnboardingReferralCode
      @skip="handleUpdatePayoutMethods"
      @apply="handleUpdatePayoutMethods"
    /> -->
	</div>
</template>
<script>
import { defineComponent, computed, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'

// Composables
import { useShowPayoutMethods, usePayoutMethods } from 'composable/onboarding'
import { useSetting } from 'store/pinia/setting'
import { useOnboarding } from 'store/pinia/onboarding'
import { useMultipleCompany } from 'store/pinia/layouts/multiple-company'

// Components
import Button from 'bbUi/components/button/button.vue'
import PayoutList from 'components/Onboarding/PayoutList.vue'
import OnboardingReferralCode from 'components/Features/Onboarding/OnboardingReferralCode.vue'

export default defineComponent({
	components: {
		Button,
		PayoutList,
		OnboardingReferralCode
	},
	async setup() {
		const storePiniaSetting = useSetting()
    const storePiniaOnboarding = useOnboarding()
		const {
			state: statePayoutMethods,
			statePopular: statePopularPayoutMethods,
			loading: loadingPayoutMethods,
			actionShow: actionShowPayoutMethods
		} = useShowPayoutMethods()
		const {
			loading: loadingUpdatePayoutMethod,
			isIncompleteForm,
			actionUpdatePayout,
			setSelectedPayoutMethods
		} = usePayoutMethods()
		const route = useRoute()
		const router = useRouter()
		const storeMultipleCompany = useMultipleCompany()

		const selectedPayouts = ref([])
		const selectedPopularPayouts = ref([])

		const payoutMethods = computed(() =>
			statePayoutMethods.value.filter(payout => {
				// remove currently disabled paper and express check payout method from the payout method list
				const excludedPayoutId = [17] // paper check(2) & express check ID
				const isExcludedPayout = excludedPayoutId.includes(payout.id) && payout.is_enable === 0
				return !isExcludedPayout
			})
		)
		const popularPayoutMethods = computed(() => statePopularPayoutMethods.value)

		await actionShowPayoutMethods()

		const handleChangeSelectedPayouts = (selectedPayoutMethods, isPopular = false) => {
			if (!Array.isArray(selectedPayoutMethods)) return

			if (isPopular) {
				selectedPopularPayouts.value = selectedPayoutMethods
			} else {
				selectedPayouts.value = selectedPayoutMethods
			}

			const payouts = [...selectedPayouts.value, ...selectedPopularPayouts.value]

			setSelectedPayoutMethods(payouts)
		}

		const handleUpdatePayoutMethods = async () => {

			await actionUpdatePayout(() => {
				storePiniaSetting.company.form = null
				storePiniaSetting.countries.result = null
				storePiniaSetting.currencies.result = null

				if (route.name.includes('newCompany')) {
					router.push({
						name: 'dashboard'
					})
				} else {
					router.push({
						name: 'welcome'
					})
				}
				// router.push({
				//   name: route.name.includes('newCompany')
				//     ? 'newCompanyDeployWidget'
				//     : 'onboardingDeployWidget'
				// })
			})
      		await storeMultipleCompany.$getCompanies(true)
		}

    const handleReferralCode = () => {
      storePiniaOnboarding.referralCode.modal = true
    }

		return {
			payoutMethods,
			popularPayoutMethods,
			loadingPayoutMethods,
			loadingUpdatePayoutMethod,
			isIncompleteForm,
			handleChangeSelectedPayouts,
			handleUpdatePayoutMethods,
			handleReferralCode
		}
	}
})
</script>
